:root {
    --yellow: #ffc600;
    --black: #272727;
  }
  
  html {
    font-family: "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
      "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-weight: 900;
    font-size: 16px;
    color: var(--black);
    text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07);
  }
  
  body {
    background-image: -webkit-linear-gradient(340deg, #ffffff, #c1b3e9);
    background-image: -o-linear-gradient(340deg, #ffffff, #c1b3e9);
    background-image: linear-gradient(110deg, #ffffff, #c1b3e9);
    
    background-position: fixed;
  }
  
  span {
    background-color: darkgrey;
    padding: 0.3rem;
    font-size: 0.8em;
    font-family: monospace;
  }
  
  p {
    line-height: 1.6em;
  }
  
  .tools {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 400px;
  }
  
  input[type=number] {
    width: 50px;
  }


  .navbar-main-1{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: aquamarine;
    color: #fff;
    height: 40px;
    padding-top: 20px;
}

.navbar-main-1 a{
    text-decoration: none;

}

.navbar-main-1-1{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    width:10%;
}

.navbar-main-1-2{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width:10%;
    padding-right:20px;
}


